(function ($) {
	"use strict";

	$(".q-apps__tabs a").each(function(){
		var ths = $(this),
			ths_tab = ths.data("tab");

		ths.on("click", function(){
			$(".q-apps__tabs a").removeClass("active-tab");
			ths.addClass("active-tab");
			if(ths_tab == "all") {
				$(".q-apps__grid--item").removeClass("hidden");
			}
			else {
				$(".q-apps__grid--item").addClass("hidden");
				$("[data-app="+ths_tab+"]").removeClass("hidden");
			}
		});
	});

}(jQuery));


// stagger in app badges in hero banner
(function ($) {
	var tl = new TimelineLite();
	var appBadges = Array.prototype.slice.call(document.querySelectorAll(".app-badge"));

	var viewportWidth = $(window).width();

	if (viewportWidth > 970) {
		appBadges.slice(0,8).forEach(function (currentValue) {
			currentValue.classList.add("row1");
		});
		appBadges.slice(8,16).forEach(function (currentValue) {
			currentValue.classList.add("row2");
		});
		appBadges.slice(16,24).forEach(function (currentValue) {
			currentValue.classList.add("row3");
		});
	} else if (viewportWidth > 855) {
		appBadges.slice(0,7).forEach(function (currentValue) {
			currentValue.classList.add("row1");
		});
		appBadges.slice(7,14).forEach(function (currentValue) {
			currentValue.classList.add("row2");
		});
		appBadges.slice(14,21).forEach(function (currentValue) {
			currentValue.classList.add("row3");
		});
	} else if (viewportWidth > 680) {
		appBadges.slice(0,6).forEach(function (currentValue) {
			currentValue.classList.add("row1");
		});
		appBadges.slice(6,12).forEach(function (currentValue) {
			currentValue.classList.add("row2");
		});
		appBadges.slice(12,18).forEach(function (currentValue) {
			currentValue.classList.add("row3");
		});
	} else if (viewportWidth > 575) {
		appBadges.slice(0,5).forEach(function (currentValue) {
			currentValue.classList.add("row1");
		});
		appBadges.slice(5,10).forEach(function (currentValue) {
			currentValue.classList.add("row2");
		});
		appBadges.slice(10,15).forEach(function (currentValue) {
			currentValue.classList.add("row3");
		});
	} else if (viewportWidth <= 575) {
		appBadges.slice(0,4).forEach(function (currentValue) {
			currentValue.classList.add("row1");
		});
		appBadges.slice(4,8).forEach(function (currentValue) {
			currentValue.classList.add("row2");
		});
		appBadges.slice(8,12).forEach(function (currentValue) {
			currentValue.classList.add("row3");
		});
	}

	function staggerInAppBadges() {
		// Unhide app badges
		Array.prototype.forEach.call(appBadges, function(el){
			el.classList.remove("hidden");
		});

		// stagger in app badges
		tl.staggerFromTo(
			".app-badge.row1", // stagger first row of app badges
			0.6, // duration
			{
				y: "20%",
				autoAlpha: 0
			},
			{
				y: "0%",
				autoAlpha: 1,
				ease: window.Power4.easeInOut,
				onComplete: function () {
				}
			},
			0.1, // stagger duration between elements
			0 // start immediately
		).staggerFromTo(
			".app-badge.row2", // stagger second row of app badges
			0.6, // duration
			{
				y: "20%",
				autoAlpha: 0
			},
			{
				y: "0%",
				autoAlpha: 1,
				ease: window.Power4.easeInOut,
				onComplete: function () {
				}
			},
			0.1, // stagger duration between elements
			0.45 // start after 0.45 seconds
		).staggerFromTo(
			".app-badge.row3", // stagger third row of app badges
			0.6, // duration
			{
				y: "20%",
				autoAlpha: 0
			},
			{
				y: "0%",
				autoAlpha: 1,
				ease: window.Power4.easeInOut,
				onComplete: function () {
				}
			},
			0.1, // stagger duration between elements
			0.79 // start after 0.79 seconds
		);
	}

	var hero = document.querySelector(".q-default-hero");
	// when the hero content animation is complete, stagger in the app badges
	hero.addEventListener("heroContentComplete", staggerInAppBadges);
}(jQuery));

